@import "ScssHelpers/helpers-all.scss";

.section--progress_mod {
	padding-top: 3rem;

	@include bp($point_2, min) {
		padding-bottom: 11.2rem;
	}
}

.step_progress {
	width: 100%;
	height: 0.8rem;
	background-color: $gray_3;
	overflow: hidden;

	@include bp($point_2, min) {
		position: relative;
		margin-bottom: 5.9rem;
		border-radius: 2px;
	}

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		margin-bottom: 2.6rem;
	}
}

.step_progress__line {
	position: absolute;
	width: 100%;
	height: 100%;
	left: -100%;
	background-color: $gold_2;
	border-radius: 2px;
}
